<script>
import { onMount } from 'svelte';
import { Link, navigate } from 'svelte-routing';

export let pxl;

async function upload(event) {
  const element = event.target;
  const regions = await pxl.detect_by_file(element);
  navigate(`/url/${encodeURIComponent(`https://img.pxl.ai/${regions[0].id.split('_')[0]}`)}`);
}

// ui.js
let imageURL = '';

function enterImageURL() {
  if (imageURL.length > 0) {
    view_demo(imageURL);
  }
}

function view_demo(url) {
    if (window.localStorage.getItem("apikey") == null) {
        alert('No API Key has been configured. Use the configuration dialog to set an API key.')
        return
    }

    var image_url = url;
    navigate("/url/" + encodeURIComponent(image_url));
}

// index.html
if (window.localStorage.getItem("local")  === null) {
  window.localStorage.setItem("local", "{{ local }}")
}
function zero_pad(n, width) {
  n = n + '';
  return n.length >= width ? n : new Array(width - n.length + 1).join('0') + n;
}

var demoNum = 16;
var demoImgNum = 50;

var demoImgArr = [];

for (var i=1; i<=demoImgNum; i++) {
  var demoImg = 'demo_' + zero_pad(i, 2) + '.jpg';
  demoImgArr.push(demoImg);
}

/* get array of random demo-images */
function shuffle(arr) {
  var ranNum, temp, i;
  for (i=arr.length - 1; i>0; i--) {
    ranNum = Math.floor(Math.random() * (i + 1));
    temp = arr[i];
    arr[i] = arr[ranNum];
    arr[ranNum] = temp;
  }
  return arr;
}

let imgArr = shuffle(demoImgArr);
</script>

<div class="description">
  <div><b>Artificial Intelligence</b></div>
  <div>Visual <b>Fashion</b> Analysis Platform</div>
</div>
<form class="user-inputs" on:submit|preventDefault={enterImageURL}>
  <input class="shadow-sm" type="text" id="input_url" bind:value={imageURL} placeholder="IMAGE URL">
  <a class="button-upload" href="javascript:document.getElementById('file').click()"><i class="fas fa-file-upload"></i></a>
  <input type="file" name="file" id="file" on:change={upload} style="display: none;">        
</form>
<div class="demo">
  <div class="label">Demo Images</div>
  <div class="list">
    {#each imgArr.slice(0, 18) as img}
      <div class="grid-item">
        <Link to="/url/{encodeURIComponent(`https://viewer.pxl.ai/demos_new/${img}`)}">
          <img class="thumbnail" src="/images/spacer.gif" style="background-image: url('https://img.pxl.ai/https://viewer.pxl.ai/demos_new/{img}');" decode="async" alt="">
        </Link>
      </div>
    {/each}
  </div>
</div>
