<script>
import { LABELS, CATEGORY } from './constant.js';

export let image_url;
export let regions;
export let analyze;
export let current_region;
export let change_region;

function get_element_id(region) {
  var elem_id = "detection_" + region.category.code + "_" + region.id;
  elem_id = elem_id.replace(/,/gi, "_");
  return elem_id;
}

function getCategoryName(region) {
  const categoryCode = region.category.code;
  const category = LABELS[categoryCode];
  if (category === undefined) {
    return CATEGORY[region.category.code.toString()].str;
  }
  const categoryName = LABELS[category]['en-US'];
  return categoryName;
}

function getCurrentAttributes(currentRegion, analyzeResult) {
  const currentAnalyze = analyzeResult.filter(a => a.id === currentRegion.id);
  let attribute = [];
  if (currentAnalyze.length > 0) {
    attribute = currentAnalyze[0].attributes;
  }
  return attribute;
}
</script>

<div id="detect-result" class="primary">
  <div class="cropper-view">
    <div class="image-view" id="image_view" style="">
      {#if image_url}
      <img src={image_url} class="thumbnail-view" id="thumbnail_view" alt="">
      {/if}
      {#if current_region}
      <div id="crop-area" style="
        top: {current_region.ry1 * 100}%;
        left: {current_region.rx1 * 100}%;
        right: {(1 - current_region.rx2) * 100}%;
        bottom: {(1 - current_region.ry2) * 100}%;
      ">
        <div class="dimmer-top"></div>
        <div class="dimmer-right"></div>
        <div class="dimmer-bottom"></div>
        <div class="dimmer-left"></div>
      </div>
      {/if}
    </div>
  </div>
  <div class="results" id="results">
    <div id="results_detection">
      <div class="label">ITEMS</div>
      <div id="detection_list">
        {#each regions as region}
        <div id={get_element_id(region)} class="item" class:select={region.id === current_region.id}>
          <a href="#" on:click|preventDefault={change_region(region)}>
            {getCategoryName(region)}
            <b>{parseInt(100 * (region.category.score || region.score))}%</b>
          </a>
        </div>
        {/each}
      </div>
    </div>
    {#if current_region && analyze.length > 0}
    <div id="attribute_list">
      <div class="divider"></div>
      <div class="label">ATTRIBUTES</div>
      <ul>
        {#each getCurrentAttributes(current_region, analyze) as attr}
        <li>
          <div class="value">#{attr.en}</div>
          <div class="type">{attr.type}</div>
        </li>
        {/each}
      </ul>
    </div>
    {/if}
  </div>
</div>
