class PXL {
  constructor(apiBase, apikey, errorHandler) {
    this.set(apiBase, apikey);
    this.errorHandler = errorHandler;
  }

  set(apiBase, apikey) {
    this.apiBase = apiBase;
    this.apikey = apikey;
  }

  detect(url) {
    return this.request(`/detect?url=${url}`);
  }

  detect_by_file(element) {
    const formData = new FormData();
    formData.append('file', element.files[0]);
    return this.request(`/detect`, {
      method: 'POST',
      body: formData,
    });
  }

  analyze(url) {
    return this.request(`/analyze?url=${url}&lang=en`);
  }

  search(region, gender, search_category, flex_query, flex_mode, count) {
    // TODO: add support for flex_ things
    let category = search_category; // v1 API
    if (this.apiBase.indexOf('/v2') > -1) {
      category = region.category.code
    }
    return this.request(`/search/${region.id}?gender=${gender}&category=${category}&count=${count}`);
  }

  recommend(region, mode, search_category, count) {
    return this.request(`/recommend/stylist/${region.id}?mode=${mode}&count=${count}&category=${search_category}`);
  }

  request(query, options) {
    const re_apikey = /^[A-Z0-9]+[a-f0-9]{56,64}$/g;
    const is_apikey = this.apikey.match(re_apikey) ? 1 : 0;
    return fetch(`${this.apiBase}${query}`, {
      ...options,
      headers: {
        [is_apikey ? "ApiKey" : "bucketname"]: this.apikey,
      },
    }).then(res => res.json()).then(json => {
      if (json.status === false) {
        this.errorHandler && this.errorHandler(json.message);
        return null;
      }
      return json.list;
    }).catch(err => {
      this.errorHandler && this.errorHandler(`${err.name}: ${err.message}`);
      return null;
    });
  }
}

export default PXL;
