<script>
import { link } from 'svelte-routing';

export let product;

function getPosition(product) {
  if (product.region !== undefined) { // v1 API
    return `${Math.floor((product.region[0] + (product.region[2] - product.region[0]) / 2) / 500 * 100)}%`;
  }
  return '50% 50%';
}

function commify(n) {
  var reg = /(^[+-]?\d+)(\d{3})/;
  n += '';
  while (reg.test(n)) n = n.replace(reg, '$1' + ',' + '$2');
  return n;
}
</script>

<div class="grid-item">
  <img class="thumbnail" src="/images/spacer.gif" style="
    background-image: url('https://img.pxl.ai/{product.image_url}');
    background-position: {getPosition(product)};
  " decode="async" alt="">
  <a href="/url/{encodeURIComponent(product.image_url)}?cc={product.category_code}" use:link>
    <div class="view-button"><img src="/images/icon_search.svg" alt="Search"></div>
  </a>
  <div class="name"><a href="{product.product_url}">{product.name.replace(/<\/?[^>]+(>|$)/g, "")}</a></div>
  <div class="price"><a href="{product.product_url}">{commify(product.price)} KRW</a></div>
</div>
