<script>
import { ONEPIECE, SKIRTS, CATEGORY, ATTRIBUTE, FLEX_MODE, DL_PROXY } from './constant.js';
import { onMount } from 'svelte';
import Detect from './Detect.svelte';
import TypeTab from './TypeTab.svelte';
import Filter from './Filter.svelte';
import ProductList from './ProductList.svelte';

export let location;
export let url;
export let pxl;

let current_result_type = 'search' || 'recommend' || 'recommend-multi';
let current_categories = undefined;
let current_gender = undefined;
let current_subcategory = undefined;
let current_region = undefined;
let current_attributes = undefined;
let flexmodeOption = 'filter';
$: flexmode = FLEX_MODE[flexmodeOption];
let regions = [];
let analyze = [];
let image_url = url;
$: if (url) {
  render();
};
$: results = null;
let resultLoading = false;

onMount(render);

async function render() {
    image_url = decodeURIComponent(url);

  var detection_cb = async function (results) {
    if (results === null) {
      return;
    }

    regions = results;
    const bestRegion = regionArgMax(regions);
    init_current_values(bestRegion);
    change_region(bestRegion);

    if (regions.length > 0 && bestRegion.id !== undefined && typeof bestRegion.id === 'string') {
      var image_hash = bestRegion.id.split('_')[0];
      analyze = await pxl.analyze(`https://img.pxl.ai/${image_hash}`);
    }
  };

  detection_cb(await pxl.detect(image_url))
}

// https://github.com/ocsdk/shuqstyle-js-sdk/blob/master/pxl.js#L574
function regionArgMax(l) {
  // Favor big rectangles. The abs() call here is overkill, but I don't trust the data.
  return argmax(l, (e) => {
    return Math.abs(e.rx2 - e.rx1) * Math.abs(e.ry2 - e.ry1) * e.category.score
  });
}

// https://github.com/ocsdk/shuqstyle-js-sdk/blob/master/pxl.js#L536
function argmax(l, f) {
  // FIXME: This should be made to work with NodeLists.
  let ms = f(l[0]),
      mo = l[0];

  l.map((e) => {
    let i = f(e);

    if (i > ms) {
      ms = i;
      mo = e;
    }
  });

  return mo;
}

  function change_region(region) {
    init_current_values(region);

    update_result();
  }

function update_type(event) {
  const element = event.target;
  const type = element.getAttribute('data-type');
  current_result_type = type;
  localStorage.setItem('current_result_type', type);
  update_result();
}

function init_current_values(region) {
  current_region = region;
  current_gender = region.gender.code;
  current_categories = [region.category.code];
  current_subcategory = undefined;
  current_attributes = [];
  flexmodeOption = 'filter';
}

function update_result() {
  if (current_result_type === 'search') {
    search(current_region, current_gender, current_categories, current_subcategory, current_attributes, flexmode);
  } else if (current_result_type === 'recommend' || current_result_type === 'recommend-multi') {
    recommend(current_region, current_gender, current_categories[0]);
  }
}

async function search(region, gender, categories, sub_category, attributes, flex_mode) {
  var gendered_category = gender;

  for (var i=0; i<categories.length; i++) {
    gendered_category |= categories[i];
  }

  var flex_query = {};

  if (sub_category !== undefined) {
    flex_query["sub_category"] = sub_category;
  }

  if (attributes && attributes.length > 0) {
    flex_query["attributes"] = attributes;
  }

  if (Object.keys(flex_query).length === 0) {
    flex_query = undefined;
  }
  results = null;
  resultLoading = true;
  const r = await pxl.search(region, gender, gendered_category, flex_query, flex_mode, 34);
  if (r !== null && r[parseInt(gendered_category)] !== undefined) { // v1 API
    results = r[parseInt(gendered_category)];
  } else {
    results = r;
  }
  resultLoading = false;
}

async function recommend(region, gender, category) {
  var gendered_category = gender | category;
  var mode = current_result_type === 'recommend' ? 1 : 2;
  results = null;
  resultLoading = true;
  const r = await pxl.recommend(region, mode, gendered_category, 34);
  results = r;
  resultLoading = false;
}
</script>

<div id="view">
  <Detect image_url={image_url} regions={regions} analyze={analyze} current_region={current_region} change_region={change_region} />
  <TypeTab bind:type={current_result_type} click_tab={update_type} pxl={pxl} />
  <Filter
    type={current_result_type}
    region={current_region}
    bind:genderOption={current_gender}
    bind:categoryOption={current_categories}
    bind:subCategoryOption={current_subcategory}
    bind:flexmodeOption={flexmodeOption}
    onFilterChange={update_result} />
  <ProductList json={results} resultLoading={resultLoading} />
</div>

<style>
#view {
  position: relative;
}
</style>
