<script>
import { Router, Route, navigate } from "svelte-routing";
import PXL from './PXL.js';
import Header from './Header.svelte';
import Index from './Index.svelte';
import View from './View.svelte';
import ErrorMessage from './ErrorMessage.svelte';

let apiBase = localStorage.getItem('apiBase') || 'https://api.pxl.ai/v2';
let apikey = localStorage.getItem('apikey') || 'INTR1e15121d9b1f2b412244024d8ccd2a3c8b186fcdd1c5ab92bce8032d';
let errorMessage = null;
const pxl = new PXL(apiBase, apikey, errorHandler);
$: pxl.set(apiBase, apikey);

if (window.location.href.indexOf('?') > -1 && window.location.href.indexOf('base=') > -1 && window.location.href.indexOf('key=') > -1) {
  const params = window.location.href.split('?')[1].split('&');
  apiBase = params.filter(q => q.indexOf('base') === 0)[0].split('=')[1];
  apikey = params.filter(q => q.indexOf('key') === 0)[0].split('=')[1];
  localStorage.setItem('apiBase', apiBase)
  localStorage.setItem('apikey', apikey)
  navigate(window.location.href.split('?')[0]);
}

function errorHandler(err) {
  if (err === undefined) {
    errorMessage = 'No results.';
    return;
  }
  errorMessage = err;
}
</script>

<Router>
  <Header bind:apiBase={apiBase} bind:apikey={apikey} />
  <Route path="/url/:url" component={View} pxl={pxl}></Route>
  <Route path="/"><Index pxl={pxl} /></Route>
  <ErrorMessage bind:errorMessage={errorMessage} />
</Router>
