<script>
import { link } from "svelte-routing";

export let apiBase;
export let apikey;

function enterApikey() {
  if (apikey.length > 0) {
    toggle_api_key_scrollin();
  }
}

let isSettingOpen = false;

function toggle_api_key_scrollin() {
  isSettingOpen && window.localStorage.setItem("apiBase", apiBase);
  isSettingOpen && window.localStorage.setItem("apikey", apikey);
  isSettingOpen = !isSettingOpen;
}
</script>

<div id="api-key-scrollin">
  <div id="api-setting" class:on={isSettingOpen}>
    <form class="block shadow-sm text-align-left" on:submit|preventDefault={enterApikey}>
      <label>
        API base
        <input tye="text" bind:value={apiBase} placeholder="https://api.pxl.ai/v1">
      </label>
      <label>
        API key
        <input type="text" id="input_apikey" bind:value={apikey}>
      </label>
      <input type="submit">
    </form>
  </div>
</div>
<div class="navbar" id="navbar">
  <a href="/" use:link>
    <img src="/images/logo_pxl_b.png" alt="PXL">
  </a>
  <ul class="menu">
    <li><a class="button" href="#" on:click|preventDefault={toggle_api_key_scrollin} id="settings"><i class="fas fa-cog"></i></a></li>
  </ul>
</div>

