<script>
import { GENDER_MASK, M_BIT, F_BIT, CATEGORY, CATEGORIES_FOR_RECOMMEND } from './constant.js';

let isFilterOpen = false;
export let type = 'search';
export let region;
export let genderOption = null;
export let categoryOption = [];
export let subCategoryOption = undefined;
export let flexmodeOption;
export let onFilterChange;

function toggleFilter() {
  isFilterOpen = !isFilterOpen;
}

function get_gender(gender_code) {
  if (typeof(gender_code) === "string") {
    if (gender_code === "female" || gender_code === "Female")
      return F_BIT;
    if (gender_code === "male" || gender_code === "Male")
      return M_BIT;
    if (gender_code === "unisex" || gender_code === "Unisex")
      return F_BIT | M_BIT;
    return 0;
  }
  if (typeof(gender_code) === "number") {
    if ((gender_code&GENDER_MASK) === F_BIT)
      return "Female";
    if ((gender_code&GENDER_MASK) === M_BIT)
      return "Male";
    return "Unisex";
  }
}

function click_gender(event) {
  const gender = parseInt(event.target.getAttribute('data-gender'));
  genderOption = gender;
  onFilterChange();
}

function click_category(event) {
  const category = parseInt(event.target.getAttribute('data-category'));
  if (type === 'search') {
    var pos = categoryOption.indexOf(category);
    if (pos >= 0 && categoryOption.length > 1) {
      categoryOption.splice(pos, 1);
    } else if (pos < 0) {
      categoryOption.push(category);
    }
    subCategoryOption = undefined;
  } else if (type === 'recommend' || type === 'recommend-nulti') {
    categoryOption = [category];
  }
  categoryOption = [...categoryOption];
  onFilterChange();
}

function click_sub_category(event) {
  const sub_category = parseInt(event.target.getAttribute('data-sub-category'));
  if (subCategoryOption === sub_category) {
    subCategoryOption = undefined;
  }
  else {
    subCategoryOption = sub_category;
  }
  onFilterChange();
}
</script>

<div id="search-options" class:on={isFilterOpen}>
  <button class="toggle" on:click={toggleFilter}>Filter</button>
  <div class="options">
    <div id="search-option-gender">
      <div class="label small">GENDER</div>
      <div class='gender-list'>
        {#each [M_BIT, F_BIT, GENDER_MASK] as gender}
        <a href="#" class="gender" class:select={genderOption === gender} data-gender={gender} on:click|preventDefault={click_gender}>
          {get_gender(gender)}
        </a>
        {/each}
      </div>
    </div>
    {#if region && CATEGORY[region.category.code.toString()] !== undefined}
    <!-- v1 API -->
    <div id="search-option-category">
      <div class="label small">CATEGORY</div>
      <div class='category-list'>
        {#each Object.keys(CATEGORY).filter(id => {
          if (type === 'search') {
            return CATEGORY[id].type === CATEGORY[region.category.code.toString()].type;
          } else if (type === 'recommend' || type === 'recommend-multi') {
            return CATEGORIES_FOR_RECOMMEND.includes(region.category.code) && CATEGORIES_FOR_RECOMMEND.includes(parseInt(id));
          }
          return false;
        }) as id}
          <a href="#" class="category" class:select={categoryOption.indexOf(parseInt(id)) > -1} data-category={id} on:click|preventDefault={click_category}>
            {CATEGORY[id].str}
          </a>
        {/each}
      </div>
    </div>
    {/if}
    {#if region && categoryOption.length === 1 && categoryOption[0] === region.category.code  && type !== 'recommend' && type !== 'recommend-multi' && CATEGORY[region.category.code.toString()] !== undefined}
    <!-- v1 API -->
    <div id="search-option-subcategory" class="on">
      <div class="label small">SUB CATEGORY</div>
      <div class='category-list'>
        {#each CATEGORY[region.category.code.toString()]['sub_categories'] as sub_category}
        <a href="#" class="category" class:select={subCategoryOption === sub_category.id} data-sub-category={sub_category.id} on:click|preventDefault={click_sub_category}>
          {sub_category.str}
        </a>
        {/each}
      </div>
    </div>
    {/if}
    <!-- <div id="searchable_attribute_list"></div> -->
    {#if type === 'search'}
    <div id="search_mode">
      <div class="label small">MODE</div>
      <div class="mode">
        <div class="pretty p-default p-round">
          <input type="radio" name="search_mode" value="filter" bind:group={flexmodeOption} on:change={onFilterChange}>
          <div class="state p-primary-o">
            <label>FILTER</label>
          </div>
        </div>
        <div class="pretty p-default p-round">
          <input type="radio" name="search_mode" value="naive" bind:group={flexmodeOption} on:change={onFilterChange}>
          <div class="state p-primary-o">
            <label>NAIVE</label>
          </div>
        </div>
      </div>
    </div>
    {/if}
  </div>
</div>
