<script>
import { CATEGORY } from './constant.js';
import ProductItem from './ProductItem.svelte';

export let json;
export let resultLoading;
</script>

<div id="result-list" class:loading={resultLoading}>
  {#if json === null}
    <!-- Lading state or something went worng. -->
  {:else}
    {#if Array.isArray(json) === false}
      {#each Object.keys(json) as categoryId}
        <h2>{CATEGORY[categoryId].str}</h2>
        <div class="grid-list">
          {#each json[categoryId] as product}
            <ProductItem product={product} />
          {/each}
        </div>
      {/each}
    {:else if json.length > 0 && Array.isArray(json[0])}
      {#each json as set}
        <h2>sets</h2>
        <div class="grid-list">
          {#each set as product}
            <ProductItem product={product} />
          {/each}
        </div>
      {/each}
    {:else if json.length > 0}
      <div class="grid-list">
        {#each json as product}
          <ProductItem product={product} />
        {/each}
      </div>
    {:else}
      No results.
    {/if}
  {/if}
</div>
